export class IDataError {
  public code: string;
  public innererror: IDataInnerError;
  public message: IDataErrorMessage;
}

export class IDataInnerError {
  public internalexception: IDataInnerError;
  public message: string;
  public stacktrace: string;
  public type: string;
}

export class IDataErrorMessage {
  public lang: string;
  public value: string;
}

export default class ODataErrorResponse {
  public error: IDataError;
}
